import React, { useState } from "react"
import "../App.css"
import Texto from "./Texto"
import { NoPrint } from "react-easy-print"

function Personal({ personal, setPersonal }) {
  const handleChange = (e) => {

    setPersonal({
      ...personal,
      [e.target.name]: e.target.value,
    })
  }

  const [editMode, setEditMode] = useState(true)

  const handleSubmit = (e) => {
    e.preventDefault()
    setEditMode((prevMode) => !prevMode)
  }

  if (!editMode) {
    return (
      <Texto
        firstName={personal.firstName}
        lastName={personal.lastName}
        email={personal.email}
        phoneNumber={personal.phoneNumber}
        handleEdit={handleSubmit}
      />
    )
  } else {
    return (
      <NoPrint>
        <form className="box" onSubmit={handleSubmit}>
          <input
            type="text"
            name="firstName"
            placeholder="First Name"
            className="mr10"
            onChange={(e) => handleChange(e)} // tenemos que usar un segundo atributo (index) para manejar multipls values.
            required
          />

          <input
            type="text"
            name="lastName"
            placeholder="Last Name"
            className="mr10"
            onChange={(e) => handleChange(e)}
            required
          />

          <input
            type="email"
            name="email"
            placeholder="example@gmail.com"
            className="mr10"
            onChange={(e) => handleChange(e)}
            required
          />

          <input
            type="text"
            name="phoneNumber"
            placeholder="Phone Number"
            className="mr10"
            onChange={(e) => handleChange(e)}
            required
          />

          <button className="btn-save">Save</button>

          {/*       {props.personal.length - 1 === props.id && <input
          type="button"
          value = "add"
          onClick={props.handleAddInput}
          />} */}
        </form>
      </NoPrint>
    )
  }
}

export default Personal
