import React from "react"
import "../App.css"
import { NoPrint } from "react-easy-print"

export default function Texto(props) {
  if (props.firstName) {
    return (
      <div className="textSection">
        <NoPrint>
          <button className="btn-edit" onClick={props.handleEdit}>
            Edit
          </button>
        </NoPrint>
        <p>
          <span className="categoryText">First Name:</span> {props.firstName}
        </p>
        <p>
          <span className="categoryText">Last Name:</span> {props.lastName}
        </p>
        <p>
          <span className="categoryText">Email:</span> {props.email}
        </p>
        <p>
          <span className="categoryText">Phone Number:</span>
          {props.phoneNumber}
        </p>
      </div>
    )
  } else if (props.company) {
    return (
      <div className="textSection">
        <NoPrint>
          <button className="btn-edit" onClick={props.handleEdit}>
            Edit
          </button>
        </NoPrint>
        <p>
          <span className="categoryText">Company Name:</span> {props.company}
        </p>
        <p>
          <span className="categoryText">Position:</span>
          {props.position}
        </p>
        <p>
          <span className="categoryText">From:</span> {props.from}
        </p>
        <p>
          <span className="categoryText">To:</span> {props.to}
        </p>
        <p>
          <span className="categoryText">Task:</span> {props.tasks}
        </p>
      </div>
    )
  } else {
    return (
      <div className="textSection">
        <NoPrint>
          <button className="btn-edit" onClick={props.handleEdit}>
            Edit
          </button>
        </NoPrint>
        <p>
          <span className="categoryText">School Name:</span> {props.schoolName}
        </p>
        <p>
          <span className="categoryText">Study:</span> {props.title}
        </p>
        <p>
          <span className="categoryText">From: </span>
          {props.from}
        </p>
        <p>
          <span className="categoryText">To:</span> {props.to}
        </p>
      </div>
    )
  }
}
