import React, { useState } from "react"
import "./App.css"
import Personal from "./components/Personal"
import Education from "./components/Education"
import Experience from "./components/Experience"
import PrintProvider, { NoPrint, Print } from "react-easy-print"
import Footer from "./components/Footer"
import Header from "./components/Header"
import { ThemeProvider } from "styled-components"
import Theme from "./theme/Theme"
/* import GlobalStyle from "./theme/GlobalStyle" */

function App() {
  const [personal, setPersonal] = useState([
    { firstName: "", lastName: "", email: "", phoneNumber: "" },
  ])
  const [education, setEducation] = useState([
    { schoolName: "", title: "", from: "", to: "" },
  ])
  const [experience, setExperience] = useState([
    { company: "", position: "", from: "", to: "", tasks: "" },
  ])

  const handleAddInput = (type) => {
    if (type === "education") {
      setEducation([
        ...education, // se hace una copia de los datos cada vez que queramos modificar el state.
        { schoolName: "", title: "", from: "", to: "" },
      ])
    } else
      setExperience([
        ...experience,
        { company: "", position: "", from: "", to: "", tasks: "" },
      ])
  }

  return (
    <ThemeProvider theme={Theme}>
      {/* <GlobalStyle /> */}
      <Header />
      <PrintProvider>
        <div className="App">
          <NoPrint></NoPrint>
          <div className="sheet">
            <NoPrint>
              <h1>Create your CV for Free!</h1>
            </NoPrint>

            <Print>
              <h2>Personal Information</h2>
              <hr />
            </Print>
            <Personal personal={personal} setPersonal={setPersonal} />
            <Print>
              <h2>Education</h2>
              <hr />
            </Print>

            {education.map((e, index) => (
              <Education
                key={index}
                item={e}
                id={index}
                education={education}
                setEducation={setEducation}
              />
            ))}
            <NoPrint>
              <button
                className="btn-add"
                value="add"
                onClick={() => handleAddInput("education")}
              >
                Add
              </button>
            </NoPrint>

            <Print>
              <h2>Experience</h2>
              <hr />
            </Print>

            {experience.map((e, index) => (
              <Experience
                key={index}
                item={e}
                id={index}
                experience={experience}
                setExperience={setExperience}
              />
            ))}

            <NoPrint>
              <button className="btn-add" value="add" onClick={handleAddInput}>
                Add
              </button>
            </NoPrint>

            <NoPrint>
              <button
                onClick={() => {
                  window.print()
                }}
                className="print"
              >
                Print
              </button>
            </NoPrint>
            {/*   <pre>
      {JSON.stringify(personal, null, 2)}
    </pre> */}
          </div>
        </div>
        <Footer />
      </PrintProvider>
    </ThemeProvider>
  )
}

export default App
