import React, { useState } from "react"
import "../App.css"
import Texto from "./Texto"
import { NoPrint } from "react-easy-print"

function Education({ education, setEducation, id, item }) {
  const handleChange = (e, index) => {
    // it will update the value based on the index
    const { name, value } = e.target
    const list = [...education]
    list[index] = {
      ...list[index],
      [name]: value,
    }
    setEducation(list)
  }

  const handleRemoveInput = (index) => {
    const list = [...education]
    list.splice(index, 1)
    setEducation(list)
  }

  const [editMode, setEditMode] = useState(true)

  const handleSubmit = (e) => {
    e.preventDefault()
    setEditMode((prevMode) => !prevMode)
  }

  if (!editMode) {
    return (
      <Texto
        schoolName={item.schoolName}
        title={item.title}
        from={item.from}
        to={item.to}
        handleEdit={handleSubmit}
      />
    )
  }
  return (
    <NoPrint>
      <form key={id} className="box" onSubmit={handleSubmit}>
        {/* se coloca el submit en el formulario y me llame a una funcion para saber que hacer con ese formulario input */}
        <input
          type="text"
          name="schoolName"
          placeholder="School Name"
          className="mr10"
          value={item.schoolName}
          onChange={(e) => handleChange(e, id)} // tenemos que usar un segundo atributo (index) para manejar multipls values.
          required
        />
        <input
          type="text"
          name="title"
          placeholder="Title of study"
          className="mr10"
          value={item.title}
          onChange={(e) => handleChange(e, id)}
          required
        />
        <input
          type="date"
          name="from"
          placeholder="From"
          className="mr10"
          value={item.from}
          onChange={(e) => handleChange(e, id)}
          required
        />
        <input
          type="date"
          name="to"
          placeholder="To"
          className="mr10"
          value={item.to}
          onChange={(e) => handleChange(e, id)}
          required
        />
        <button className="btn-save">Save</button>
        {education.length > 1 && (
          <input
            type="button"
            value="remove"
            className="btn-remove"
            onClick={() => handleRemoveInput(id)}
          />
        )}
        {/*       {props.education.length - 1 === id && <input
          type="button"
          value = "add"
          onClick={props.handleAddInput}
          />} */}
      </form>
    </NoPrint>
  )
}

export default Education
