import React, { useState } from "react"
import "../App.css"
import Texto from "./Texto"
import { NoPrint } from "react-easy-print"

function Experience({ setExperience, experience, item, id }) {
  const handleChange = (e, index) => {
    // it will update the value based on the index
    const { name, value } = e.target
    const list = [...experience]
    list[index][name] = value
    setExperience(list)
  }

  const handleRemoveInput = (index) => {
    const list = [...experience]
    list.splice(index, 1)
    setExperience(list)
  }

  const [editMode, setEditMode] = useState(true)

  const handleSubmit = (e) => {
    e.preventDefault()
    setEditMode((prevMode) => !prevMode)
  }

  if (!editMode) {
    return (
      <Texto
        company={item.company}
        position={item.position}
        from={item.from}
        to={item.to}
        tasks={item.tasks}
        handleEdit={handleSubmit}
      />
    )
  }
  return (
    <NoPrint>
      <form key={id} className="box" onSubmit={handleSubmit}>
        <input
          type="text"
          name="company"
          placeholder="Company"
          className="mr10"
          value={item.company}
          onChange={(e) => handleChange(e, id)} // tenemos que usar un segundo atributo (index) para manejar multipls values.
          required
        />

        <input
          type="text"
          name="position"
          placeholder="Position"
          className="mr10"
          value={item.position}
          onChange={(e) => handleChange(e, id)}
          required
        />

        <input
          type="date"
          name="from"
          placeholder="From"
          className="mr10"
          value={item.from}
          onChange={(e) => handleChange(e, id)}
          required
        />

        <input
          type="date"
          name="to"
          placeholder="To"
          className="mr10"
          value={item.to}
          onChange={(e) => handleChange(e, id)}
          required
        />

        <input
          type="text"
          name="tasks"
          placeholder="Tasks"
          className="mr10"
          value={item.tasks}
          onChange={(e) => handleChange(e, id)}
          required
        />

        <button className="btn-save">Save</button>

        {experience.length > 1 && (
          <input
            type="button"
            value="remove"
            className="btn-remove"
            onClick={() => handleRemoveInput(id)}
          />
        )}

        {/*       {props.experience.length - 1 === id && <input
          type="button"
          value = "add"
          onClick={props.handleAddInput}
          />} */}
      </form>
    </NoPrint>
  )
}

export default Experience
